import React from "react";
import { MdDelete } from "react-icons/md";
import { FaEdit } from "react-icons/fa";
import DeleteModal from "../Modal/DeleteModal";
import { Link } from "react-router-dom";
import { IMAGES, SVGICON } from "../../constant/theme";
import useStorage from "../../../hooks/useStorage";

const CompanyTable = ({
  tableData,
  tempValue,
  onConfirmDelete,
  editDrawerOpen,
  setDataLength
}) => {
  const { getBranch } = useStorage();
  var filterData = tableData;
  if (tempValue !== "All") {
    filterData = tableData.filter(
      (item) => item.role === "company" && item.parent === tempValue
    );
  }
  setDataLength(filterData.length)
  return (
    <>
      {filterData.map((item, index) => (
        <tr key={index}>
          <td>
            <span>{item.id}</span>
          </td>
          <td>
            <span className="text-primary">{item.userName}</span>
          </td>
          <td>
            <div className="products">
              {/* <img
                src={item.image || IMAGES.contact1}
                className="avatar avatar-md"
                alt=""
              /> */}
              <div>
                <h6>{item.parent}</h6>
              </div>
            </div>
          </td>
          <td>
            <span>{item.mobileNumber}</span>
          </td>
          {/* <td>
                        <span>{item.usergroup || item.shortName}</span>
                    </td> */}
          <td>
            <span>{item.country}</span>
          </td>
          <td>
            <span>{item.email}</span>
          </td>

          {/* <td>
                        <span style={{width:"5rem"}} className={`badge light border-0 ${item.status === "Active" ? 'badge-success' : 'badge-danger'} `} >{item.status || "Inactive"}</span>
                    </td> */}
          <td>
            <Link
              to={`/branch/cid/${item.id}`}
              className="text-primary badge light border-0 badge-count"
            >
              {getBranch(item.userName)}
            </Link>
          </td>
          <td>
            <span>{item.zipCode}</span>
          </td>
          <td>
            <span className="d-flex justify-content-center">
              <span
                className="cursor-pointer"
                onClick={() => editDrawerOpen(item.id)}
              >
                <FaEdit style={{ color: "green", fontSize: "1.2rem" }} />
              </span>
              <DeleteModal
                className="cursor-pointer "
                onConfirmDelete={onConfirmDelete}
                id={item.id}
              >
                <MdDelete style={{ color: "red", fontSize: "1.2rem" }} />
              </DeleteModal>
            </span>
          </td>
        </tr>
      ))}
    </>
  );
};

export default CompanyTable;
